<template>
  <div class="seePage">
    <!-- 采购计划列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">采购订单</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看采购订单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="采购组织:" size="mini">
          {{params.company_name}}
        </el-form-item>
        <el-form-item label="单据号:" size="mini">
          {{params.order_sn}}
        </el-form-item>
        <el-form-item label="来源单号:" size="mini">
          {{params.plan_sn}}
        </el-form-item>
        <el-form-item label="业务日期:" size="mini">
          {{params.business_time}}
        </el-form-item>
        <el-form-item label="供应商(厂家):" size="mini">
          {{params.supplier_name}}
        </el-form-item>
        <el-form-item label="采购仓库:" size="mini">
          {{ params.warehouse_name }}
        </el-form-item>
        <el-form-item label="订单类型:" size="mini">
          {{ params.order_type_name }}
        </el-form-item>
        <el-form-item label="部门:" size="mini">
          {{params.department_name}}
        </el-form-item>
        <el-form-item label="采购员:" size="mini">
          {{params.buyer_name}}
        </el-form-item>
        <el-form-item label="支付方式:" size="mini">
          {{params.pay_method_name}}
        </el-form-item>
        <el-form-item label="付款方式:" size="mini">
          {{params.receivables_mode_name}}
        </el-form-item>
        <el-form-item label="审核状态:" size="mini">
          {{params.auditor_status_name}}
        </el-form-item>
        <el-form-item label="备注:" size="mini">
          {{params.remark}}
        </el-form-item>
        <el-form-item label="商城订单号" size="mini" v-if='is_company_service'>
          {{params.ec_order_sn}}
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" label="序号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_code" label="工厂代码" width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购价" align="right">
          <template slot-scope="scope">
            {{scope.row.purchase_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="小计金额" align="right">
          <template slot-scope="scope">
            {{scope.row.subtotal_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>
    </div>
    <div v-if="params.ec_order_id">
      <ul class="Logistics">
        <li><span>收货信息:</span>{{activities.reciver_info||''}}</li>
        <li><span>物流公司:</span>{{activities.shipping_express_name||''}}</li>
        <li><span>物流单号:</span>{{activities.shipping_code||''}}</li>
      </ul>
      <el-timeline :reverse="reverse">
        <el-timeline-item v-for="(activity, index) in activities.list" :key="index" :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </div>
    <div class="refundauditFoot">
      <div style="text-align: left;">
        使用返利: {{ params.rebate_price }} &nbsp;&nbsp;&nbsp; 余额：{{ params.rebate_balance }}
      </div>
      <span>创建人：{{params.add_user_name}}</span>
      <span>创建时间：{{params.add_time}}</span>
      <span>审核人：{{params.auditor_name}}</span>
      <span>审核时间：{{params.auditor_time}}</span>
      <span>修改人：{{params.update_user_name}}</span>
      <span>修改时间：{{params.update_time}}</span>
    </div>

    <p style="text-align:center;padding-top:30px;">
      <!--未审核-->
      <template v-if="params.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <!-- <el-button type="primary" v-if='order_id && authority.b_turn && params.is_relation != 1' @click="turnSaleOrder(params)">转 单</el-button> -->
      <el-button @click="goBack">返 回</el-button>
    </p>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { common } from "./js/tableHead";
export default {
  name: "purchaseOrderSee",
  mixins: [common],
  data() {
    return {
      authority: {},
      TableData: {},
      chaid: false,
      Logistics: {},
      reverse: true,
      activities: {},
      params: {},
    };
  },
  computed: {
    ...mapGetters(["orderSeeGet"]),
  },
  methods: {
    ...mapActions(["orderIndexACT"]),
    goBack() {
      //缺少id
      this.$router.push({ path: "/purchase/order" });
    },
    getLogInfo(val) {
      this.$axMethod
        .getLogisticsInfo({
          ec_order_id: val,
        })
        .then((res) => {
          this.activities = res.data.data;
        });
    },

    examineFun() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.orderAuditorOrder,
        data: {
          order_id: this.order_id,
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success",
          });
          this.goBack();
        }
      });
    },
    closeF() {
      this.$axMethod
        .closePurchaseOrder({
          order_id: this.order_id,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success",
            });
            this.goBack();
          }
        });
      //关闭
    },
    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.orderAuditorInvalid,
        data: {
          order_id: this.orderSeeGet.order_id,
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success",
          });
          this.goBack();
        }
      });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]);
          } else if (index == 6) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    /** 采购订单详情 */
    this.order_id = this.orderSeeGet.order_id;
    this.$axios({
      method: "get",
      url: this.$api.getOrderInfoPurchase,
      params: {
        order_id: this.order_id,
      },
    }).then((res) => {
      if (res.data.code == 200) {
        this.TableData = res.data.data;
        this.params = res.data.data.info;
        this.getRebatePrice();
        if (this.params.ec_order_id) {
          this.getLogInfo(this.params.ec_order_id);
        }
      }
    });
  },
};
</script>

