// 采购订单公共方法
import {
  mapActions,
  mapGetters
} from "vuex";
import {
  opthionList
} from "common/utils/public_function/commonUtils";
export const common = {
  data() {
    return {}
  },
  activated() {
    let _routeParams = this.$route.params
    if (_routeParams.transferType) { // 如果是通过库存预警过来转单的则执行
      this.portStockEarlyWarningChangeOrderInfo(_routeParams)
    }
  },
  methods: {
    ...mapActions(['salesorderAdd']),
    getRebatePrice() {
      if (this.params.supplier_id && this.params.company_id) {
        this.$axMethod
          .getRebatePrice({
            brand_id: -1,
            supplier_id: this.params.supplier_id,
            company_id: this.params.company_id
          })
          .then(({ data }) => {
            if (data.code == 200) {
              this.$set(
                this.params,
                "rebate_balance",
                data.data.rebate_balance
              );
            }
          });
      }
    },
    // 转单方法
    turnSaleOrder(row) {
      this.salesorderAdd({
        type: '采购单转销售订单',
        Buy_order: row,
        random: Math.floor(Math.random() * 10000) // 转单随机数
      });
      this.$router.push({
        path: "/sales/salesorder/salesorderAdd"
      });
    },
    // 库存预警转 - 单采购订单/采购入库(接口)
    portStockEarlyWarningChangeOrderInfo(params) {
      this.loading = true; // 开启加载
      this.$axMethod.stockEarlyWarningChangeOrderInfo(params.portParams).then(({
        data
      }) => {
        this.loading = false; // 关闭加载
        if (data.code == 200) {
          let _info = data.data.info,
            goods_list = data.data.goods_list;
          Object.keys(_info).forEach(key => {
            this.$set(this.params, key, _info[key])
            // this.params[key] = _info[key]
          })
          this.params.remark = _info.abstract
          if (this.params.warehouse_id == 0) this.params.warehouse_id = ''
          if (Number(localStorage.company_id) != _info.company_id) {
            this.$set(this.params, 'department_id', '') // 清空部门
            this.$set(this.params, 'buyer_id', '') // 清空采购员
          } else if (!this.params.department_id) {
            this.$set(this.params, 'department_id', Number(localStorage.department_id)) // 赋值部门
            this.$set(this.params, 'buyer_id', Number(localStorage.employee_id)) // 赋值采购员
          }
          opthionList(this, _info);
          switch (params.transferType) {
            case 1: // 转单类型（采购订单）
              if (this.orderAddGet.order_id) this.orderIndexACT({}); // 清空已有信息
              goods_list = goods_list.map(item => {
                item.unit_name && delete item.unit_name
                item.unit_id && delete item.unit_id
                item.remaining_goods_num = item.goods_num
                this.handleEdit({ row: item, column: { property: 'goods_num' } }) // 计算一遍
                return item
              })
              this.$refs.xTable._proxy("loadData", goods_list);
              break
            case 2: // 转单类型（采购入库）
              goods_list = goods_list.map(item => {
                if (!item.batch) {
                  this.$set(item, 'batch', this.getWeek())
                }
                this.$set(item, 'remaining_goods_num', item.goods_num)
                item.goods_num && delete item.goods_num
                let param = {
                  num: "remaining_goods_num", // 商品数量的key
                  price: "purchase_price", // 商品价格的key
                  totalPrice: "subtotal_price" // 商品小计金额
                };
                this.$publicFution.computeTotal(item, param, "remaining_goods_num", this); // 计算一遍
                return item
              })
              this.tableData = goods_list
              break
          }
        } else {
          // this.$message({
          //   type: "error",
          //   message: data.msg
          // });
        }
      }).catch(error => {
        console.log(error)
        this.loading = false; // 关闭加载
        this.$message({
          type: "error",
          message: '转单失败请重试'
        });
      });
    },
  },
}
